export default {
    to(name) {
        window.scrollTo({
            top: this.sections.find(section => section.name === name).val,
            behavior: 'smooth'
        })
    },
    down() {
        window.scrollTo({
            top: this.sections[this.sections.findIndex(section => section.name === this.activeSection)+1].val,
            behavior: 'smooth'
        })
    },
    up() {
        window.scrollTo({
            top: this.sections[this.sections.findIndex(section => section.name === this.activeSection)-1].val,
            behavior: 'smooth'
        })
    }
}