import ScrollSection from './Directives/ScrollSection';
import data from './data';
import methods from './methods';
import SpyListener from './SpyListener'


export default {
    install(Vue, options) {
        const root = new Vue({
            data,
            methods
        });

        Vue.directive('scroll-section', ScrollSection);
        SpyListener.listen(root);
        Vue.prototype.$scroll = root;

    }
}