let timer = null;

export default {
    listen(root) {
        let lastScrollY = window.scrollY;
        window.addEventListener('scroll', () => {
            if (!timer) {
                timer = setTimeout(() => {
                    timer = null;
                    root.sections.forEach((section, index) => {
                        if (window.scrollY >= section.val)
                            root.activeSection = section.name;
                    });
                    root.belowThreshold = window.scrollY > 100;
                }, 200);
            }
        }, { passive: true });
    }
}